/**
 * Partial for adding standard breakpoint variables
 */
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,700");
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700");
@media (min-width: 0) {
  .breakpoint-helper:after {
    content: ' ($xxs)';
  }
}

@media (min-width: 320px) {
  .breakpoint-helper:after {
    content: ' ($xs)';
  }
}

@media (min-width: 544px) {
  .breakpoint-helper:after {
    content: ' ($sm)';
  }
}

@media (min-width: 800px) {
  .breakpoint-helper:after {
    content: ' ($md)';
  }
}

@media (min-width: 960px) {
  .breakpoint-helper:after {
    content: ' ($lg)';
  }
}

@media (min-width: 1100px) {
  .breakpoint-helper:after {
    content: ' ($xl)';
  }
}

.is-vishidden {
  position: absolute !important;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

body {
  background-color: #fbfcfd;
}

/**
 * @file
 * Visual styles for tabs.
 */
div.tabs {
  margin: 0;
}

ul.tabs {
  list-style: none;
  margin: 0 0 1em 0;
  padding: 0;
}

.tabs > li {
  display: inline-block;
  margin: 0 0.3em 0 0;
  /* LTR */
}

[dir="rtl"] .tabs > li {
  margin-left: 0.3em;
  margin-right: 0;
}

.tabs a {
  display: block;
  padding: 0.2em 1em;
  text-decoration: none;
  background-color: #608fa9;
  color: white;
}

.tabs a.is-active {
  background-color: #81a6bb;
  color: white;
}

.tabs a:focus,
.tabs a:hover {
  background-color: #a2becd;
  color: #152128;
}

/**
 * @file
 * Styles for breadcrumbs.
 */
.breadcrumb {
  padding-bottom: 0.5em;
}

.breadcrumb ol {
  margin: 0;
  padding: 0;
}

[dir="rtl"] .breadcrumb ol {
  /* This is required to win over specificity of [dir="rtl"] ol */
  margin-right: 0;
}

.breadcrumb li {
  display: inline;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* IE8 does not support :not() and :last-child. */
.breadcrumb li:before {
  content: ' \BB ';
}

.breadcrumb li:first-child:before {
  content: none;
}

.js-facets-checkbox-links {
  list-style-type: none;
  padding-left: 0;
}

.js-facets-checkbox-links.content-type {
  margin-bottom: 0;
}

.js-facets-checkbox-links.media-type {
  margin-top: 0;
}

.js-facets-checkbox-links .facet-item label {
  display: inline-block;
  margin-left: 10px;
}

#views-exposed-form-acquia-search-page {
  margin-top: 10px;
}

#views-exposed-form-acquia-search-page .form-item-search {
  display: inline-block;
}

#views-exposed-form-acquia-search-page .form-item-search label {
  font-size: 1.5em;
  font-family: "Montserrat", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1.5em;
}

#views-exposed-form-acquia-search-page #edit-actions {
  display: inline-block;
}

.form-item-sort-bef-combine {
  display: table-row;
  float: none !important;
  margin-bottom: 1em;
}

.form-item-sort-bef-combine label {
  display: table-cell;
  padding-right: .5em;
}

.form-item-sort-bef-combine select {
  display: table-cell;
}

/**
 * @file
 * Styles for system messages.
 */
.messages {
  background: no-repeat 10px 17px;
  /* LTR */
  border: 1px solid;
  border-width: 1px;
  /* LTR */
  border-radius: 2px;
  padding: 0.75em;
  /* LTR */
  word-wrap: break-word;
  overflow-wrap: break-word;
  margin-bottom: 0.75em;
}

.messages + .messages {
  margin-top: 1.538em;
}

[dir="rtl"] .messages {
  border-width: 1px 0 1px 1px;
  background-position: right 10px top 17px;
  padding-left: 20px;
  padding-right: 35px;
  text-align: right;
}

.messages__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.messages__item + .messages__item {
  margin-top: 0.769em;
}

/* See .color-success in Seven's colors.css */
.messages--status {
  color: #325e1c;
  background-color: #f3faef;
  border: 1px solid #c9e1bd;
  /* LTR */
  background-image: url(../../../../misc/icons/73b355/check.svg);
  box-shadow: 0;
}

[dir="rtl"] .messages--status {
  border-color: #c9e1bd transparent #c9e1bd #c9e1bd;
  box-shadow: 8px 0 0 #77b259;
  margin-left: 0;
}

/* See .color-warning in Seven's colors.css */
.messages--warning {
  background-color: #fdf8ed;
  background-image: url(../../../../misc/icons/e29700/warning.svg);
  border-color: #f4daa6 #f4daa6 #f4daa6 transparent;
  /* LTR */
  color: #734c00;
  box-shadow: -8px 0 0 #e09600;
  /* LTR */
}

[dir="rtl"] .messages--warning {
  border-color: #f4daa6 transparent #f4daa6 #f4daa6;
  box-shadow: 8px 0 0 #e09600;
}

/* See .color-error in Seven's colors.css */
.messages--error {
  background-color: #fcf4f2;
  color: #a51b00;
  background-image: url(../../../../misc/icons/e32700/error.svg);
  border-color: #f9c9bf #f9c9bf #f9c9bf transparent;
  /* LTR */
  box-shadow: -8px 0 0 #e62600;
  /* LTR */
}

[dir="rtl"] .messages--error {
  border-color: #f9c9bf transparent #f9c9bf #f9c9bf;
  box-shadow: 8px 0 0 #e62600;
}

.messages--error p.error {
  color: #a51b00;
}

/**
 * @file
 * Utility classes to hide elements in different ways.
 */
/**
 * Hide elements from all users.
 *
 * Used for elements which should not be immediately displayed to any user. An
 * example would be collapsible details that will be expanded with a click
 * from a user. The effect of this class can be toggled with the jQuery show()
 * and hide() functions.
 */
.hidden {
  display: none;
}

/**
 * Hide elements visually, but keep them available for screen readers.
 *
 * Used for information required for screen reader users to understand and use
 * the site where visual display is undesirable. Information provided in this
 * manner should be kept concise, to avoid unnecessary burden on the user.
 * "!important" is used to prevent unintentional overrides.
 */
.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  word-wrap: normal;
}

/**
 * The .focusable class extends the .visually-hidden class to allow
 * the element to be focusable when navigated to via the keyboard.
 */
.visually-hidden.focusable:active,
.visually-hidden.focusable:focus {
  position: static !important;
  clip: auto;
  overflow: visible;
  height: auto;
  width: auto;
}

/**
 * Hide visually and from screen readers, but maintain layout.
 */
.invisible {
  visibility: hidden;
}

